:root {
  --main-color: #16bf54;
  --tag-color: #7ae1f2;
  --tag-select-color: #ffe079;
  --secondary-color: #1d1d1f;
  --text-color: #1d1d1f;
  --white-color: #ffffff;
  --black-color: #000000;
  --bg-color: #cef8ff;
  --font-graphik: "graphik", Arial, sans-serif;
  --border-color: rgba(0, 0, 0, 0.1);
  --box-shadow: 0px 4px 14px rgba(0, 0, 0, 0.05);
  --gap: 4px;
  --border-radius: 4px;
}

@font-face {
  font-family: "graphik";
  src: url("../fonts/_Graphik-400.woff2") format("woff");
  font-weight: normal;
  font-style: normal;
  font-display: swap;
}

@font-face {
  font-family: "graphik";
  src: url("../fonts/_Graphik-500.woff2") format("woff");
  font-weight: 500;
  font-style: normal;
  font-display: swap;
}

@font-face {
  font-family: "graphik";
  src: url("../fonts/_Graphik-600.woff2") format("woff");
  font-weight: 600;
  font-style: normal;
  font-display: swap;
}

@font-face {
  font-family: "graphik";
  src: url("../fonts/_Graphik-700.woff2") format("woff");
  font-weight: 700;
  font-style: normal;
  font-display: swap;
}

html {
  font-size: 18px;
}

html,
body {
  height: 100%;
}

body {
  font-family: var(--font-graphik);
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  font-weight: 400;
  line-height: 1.42;
}

ul {
  margin: 0;
  padding: 0;
}

ul li {
  list-style: none;
}

button {
  border: none;
  background: transparent;
  cursor: pointer;
  padding: 0;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  margin: 0;
  font-weight: normal;
  font-feature-settings: "pnum" on, "lnum" on;
}

table {
  border-collapse: collapse;
  border-spacing: 0;
}

p {
  margin: 0;
}

a {
  text-decoration: none;
  color: inherit;
}

* {
  box-sizing: border-box;
}

.button {
  display: flex;
  padding: calc(var(--gap) * 2) calc(var(--gap) * 6);
  font-weight: 500;
  font-size: 16px;
  border-radius: var(--border-radius);
  color: var(--white-color);
}

.button--main {
  background-color: var(--main-color);
}

.button--secondary {
  background-color: var(--text-color);
}

.buttons {
  display: flex;
}

.buttons__button:not(:last-child) {
  margin-right: calc(var(--gap) * 2);
}

.container {
  max-width: 1230px;
  width: 100%;
  margin: 0 auto;
  padding: 0 calc(var(--gap) * 7);
}

.header {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 100%;
  background-color: var(--white-color);
  min-height: 72px;
  padding: calc(var(--gap) * 3) calc(var(--gap) * 6);
  box-shadow: var(--box-shadow);
  z-index: 10;
}

.header__logo {
  width: 178px;
  height: 44px;
}

.header__logo svg {
  width: 100%;
  height: 100%;
}

.header__aside {
  display: flex;
}

.header__mobile-menu {
  display: none;
}

.header__nav {
  margin-right: calc(var(--gap) * 6);
}

.main {
  flex: 1 1 auto;
  padding-top: 72px;
}

.mobile-menu {
  display: block;
  position: relative;
  right: 0;
  top: 0;
  width: 24px;
  height: 3px;
  background-color: #999999;
  border-radius: 3px;
  transition: width 0.3s;
}

.mobile-menu:after,
.mobile-menu:before {
  content: "";
  position: absolute;
  display: block;
  width: 24px;
  height: 3px;
  border-radius: 3px;
  background-color: inherit;
  transition: width 0.3s;
}

.mobile-menu:after {
  top: -7px;
  left: 0;
}

.mobile-menu:before {
  top: 7px;
  left: 0;
}

.nav {
  display: flex;
}

.nav__list {
  display: flex;
  align-items: center;
}

.nav__item:not(:last-child) {
  margin-right: calc(var(--gap) * 6);
}

.nav__link {
  font-weight: 500;
  font-size: 16px;
  transition: color 0.3s;
}

.nav__link:hover,
.nav__link--active {
  color: var(--main-color);
}

.section {
  position: relative;
}

.section--title {
  background-color: var(--bg-color);
}

.some-block {
  padding: 206px 0;
}

.statistics {
  display: flex;
  justify-content: space-between;
  align-items: center;
  background-color: var(--white-color);
  padding: calc(var(--gap) * 9) calc(var(--gap) * 14);
  border-radius: calc(var(--border-radius) * 4.5);
  box-shadow: 0px 30px 44px rgba(45, 45, 45, 0.1);
}

.statistics__logo {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 237px;
  height: 71px;
  left: 430px;
  top: 769px;
  background: #c4c4c4;
  font-weight: 600;
  font-size: 22px;
  line-height: 155%;
}

.statistics__list {
  display: flex;
}

.statistics__item {
  text-align: center;
  padding: calc(var(--gap) * 5) calc(var(--gap) * 11);
}

.statistics__item:not(:last-child) {
  border-right: 1px solid var(--border-color);
}

.statistics__count {
  font-weight: 500;
  font-size: calc(16px + 1.6260162602vw);
  line-height: 1;
  color: var(--black-color);
}

.statistics__text {
  font-weight: 400;
  font-size: calc(16px + 0.162601626vw);
  line-height: 1.25;
}

.sub-title {
  font-weight: 600;
  font-size: calc(16px + 0.6504065041vw);
  line-height: 1.14;
  letter-spacing: -0.01em;
}

.tags {
  display: flex;
  padding: 0 calc(var(--gap) * 2) calc(var(--gap) * 2) 0;
}

.tags__item {
  position: relative;
}

.tags__item:not(:last-child) {
  margin-right: calc(var(--gap) * 3);
}

.tags__item:after {
  content: attr(data-text);
  position: relative;
  display: flex;
  align-items: center;
  padding: 7px calc(var(--gap) * 3);
  font-weight: 500;
  font-size: calc(8px + 0.7317073171vw);
  line-height: 1.1;
  border: 2px solid var(--black-color);
  box-sizing: border-box;
  border-radius: var(--border-radius);
}

.tags__item:before {
  content: "";
  position: absolute;
  display: block;
  top: 8px;
  left: 8px;
  background-color: var(--tag-color);
  width: 100%;
  height: 100%;
  border-radius: var(--border-radius);
}

.tags__item--select:before {
  background-color: var(--tag-select-color);
}

.text {
  font-weight: 600;
  font-size: calc(16px + 0.487804878vw);
  line-height: 1.55;
  letter-spacing: -0.01em;
}

.title-block {
  position: relative;
  padding: 120px 0 196px;
}

.title-block__tags {
  margin-bottom: calc(var(--gap) * 3);
}

.title-block__content {
  max-width: 59%;
}

.title-block__title {
  margin-bottom: calc(var(--gap) * 6);
}

.title-block__sub-title {
  max-width: 90%;
  display: -webkit-box;
  -webkit-line-clamp: 3;
  -webkit-box-orient: vertical;
  overflow: hidden;
  text-overflow: ellipsis;
}

.title-block__image {
  position: absolute;
  top: 50%;
  right: -8%;
  transform: translateY(-53%);
  width: 44vw;
  max-width: 611px;
}

.title-block__image:after {
  content: "";
  position: absolute;
  padding-bottom: 81.17%;
}

.title-block__image img {
  width: 100%;
  height: 100%;
  object-fit: contain;
}

.title-block__statistics {
  position: absolute;
  top: 100%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 100%;
}

.title {
  font-weight: 700;
  font-size: calc(8px + 2.6829268293vw);
  line-height: 1.14;
}

.wrapper {
  height: 100%;
  display: flex;
  overflow-x: hidden;
  flex-direction: column;
}

@media screen and (max-width: 1024px) {
  .header__logo {
    width: 129px;
    height: 32px;
  }

  .header__aside {
    display: none;
  }

  .header__mobile-menu {
    display: block;
  }

  .some-block {
    padding: 246px 0;
  }

  .statistics {
    flex-direction: column;
  }

  .statistics__list {
    flex-direction: column;
    padding-top: calc(var(--gap) * 5);
  }

  .statistics__item:not(:last-child) {
    border-right: none;
  }

  .statistics__item {
    border-top: 1px solid var(--border-color);
  }

  .title-block {
    padding: 30px 0 250px;
  }

  .title-block__statistics {
    max-width: 400px;
  }
}

@media screen and (max-width: 768px) {
  .title-block__content {
    max-width: 100%;
  }

  .title-block__image {
    position: relative;
    right: 0;
    width: 100%;
    max-width: 400px;
    transform: translateY(0);
    margin: 0 auto;
  }
}